        <el-button style="float: right" type="info" size="small" @click="$router.go(-1)">返回</el-button>
      </div>
      <div class="tablediv">

          <table border="1">
            <tbody>

                <tr>
                    <td width="105" class="key">学生姓名</td>
                    <td width="280" :title="this.$route.query.data.stuName">
                        {{$route.query.data.stuName}}
                    </td>   
                  
                
                    <td width="105" class="key">学号</td>
                    <td width="280" :title="this.$route.query.data.stuId">
                        {{$route.query.data.stuId}}
                    </td>
                 </tr>

                 <tr>
                    <td width="105" class="key">性别</td>
                    <td width="280" :title="this.$route.query.data.stuGender">
                      <span v-if="$route.query.data.stuGender == 0">男</span>
                      <span v-else>女</span>
                    </td>
                  
                
                    <td width="105" class="key">民族</td>
                    <td width="280" >
                        {{$route.query.data.stuNation}}
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">班级</td>
                    <td width="280" :title="this.$route.query.data.className">
                        {{$route.query.data.className}}
                    </td>   
                  
                
                    <td width="105" class="key">班级职务</td>
                    <td width="280" :title="this.$route.query.data.cpName">
                        {{$route.query.data.cpName}}
                    </td>
                 </tr>

                 <tr>
                    <td width="105" class="key">类别</td>
                    <td width="280" :title="this.$route.query.data.stuType">
                        {{$route.query.data.stuType}}
                    </td>
                  
                
                    <td width="105" class="key">照片</td>
                    <td width="280" >
                        <img :src="$route.query.data.stuPic" style="width:100px;heigh:100px">
                    </td>
                 </tr>  

                 <tr>
                    <td width="105" class="key">出生日期</td>
                    <td width="280" :title="this.$route.query.data.stuBirthday">
                        {{$route.query.data.stuBirthday}}
                    </td>
                  
                
                    <td width="105" class="key">入学日期</td>
                    <td width="280" >
                        {{$route.query.data.entranceDate}}
                    </td>
                 </tr> 
                <tr>
