                    <td width="280" >
                        {{$route.query.data.stuIdCard}}
                    </td>
                  
                
                    <td width="105" class="key">家庭出身</td>
                    <td width="280" >
                        {{$route.query.data.familyOrigin}}
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">政治面貌</td>
                    <td width="280" >
                        {{$route.query.data.stuPolitics}}
                    </td>
                  
                
                    <td width="105" class="key">婚否</td>
                    <td width="280" >
                        <span v-if="$route.query.data.stuGender == 0">是</span>
                      <span v-else>否</span>
                    </td>
                 </tr>  
                <tr>
                    <td width="105" class="key">高中毕业院校</td>
                    <td width="280" >
