                    <td width="280">
                        {{$route.query.data.homeAddress}}
                    </td>
                  
                    <td width="105" class="key">邮编</td>
                    <td width="280">
                        {{$route.query.data.postCode}}
                    </td>
                </tr>  
                
                
            </tbody>
        </table>
      </div>
    </el-card>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      Form: {},
    }
  },
  created() {
    this.isAuthority()
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save{
  float: right;
  margin-right: 10px;
}
table{
    border: 1px solid #d3d3d3;
    border-collapse: collapse;
    width: 100%;
    color: #606266;
    line-height: 45px;
    td{
        padding-left: 22px;
    }
}
.key{
    color: #909399;
    background-color: #f9f9f9;
}
</style>
